<template>
    <div class="white-card px-4 py-4">
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'locations' }"
            >
                Locations /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
        <b-form @submit="onSubmit" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Number" label-for="inputNumber">
                    <b-form-input
                        id="inputNumber"
                        v-model.trim="$v.form.number.$model"
                        :class="{ 'is-invalid': $v.form.number.$error }"
                    >
                    </b-form-input>
                    <b-form-invalid-feedback>
                        A valid integer with maximum length 6 is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Name" label-for="inputName">
                    <b-form-input
                        id="inputName"
                        v-model.trim="$v.form.name.$model"
                        :class="{ 'is-invalid': $v.form.name.$error }"
                    >
                    </b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
         
                <b-form-group label="Address" label-for="inputName">
                    <b-form-input
                        v-model="$v.address.name.$model"
                        ref="inputAddress"
                        :class="{ 'is-invalid': $v.address.name.$error }"
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Client" label-for="inputName">
                    <b-form-select
                        :options="clients"
                        v-model="$v.form.client.$model"
                        :class="{ 'is-invalid': $v.form.client.$error }"
                    ></b-form-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-button type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
            </div>
        </b-form>
    </div>
</template>
<script>
import { required, numeric, maxLength } from 'vuelidate/lib/validators';
import pickBy from 'lodash/pickBy';
import Backend from '../../services/backend';

const backend = new Backend();
// const google = window.google;

export default {
    name: 'LocationsAddEditPage',
    data() {
        return {
            addresses: [],
            address: '',
            clients: [],
            form: {},
            successMessage: '',
            errorMessage: '',
            errorFetching: false
        };
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    validations: {
        form: {
            number: {
                numeric,
                required,
                maxLength: maxLength(6)
            },
            name: {
                required
            },
            client: {

            }
        },
        address: {
            name: 
            {
                required
            }
        }
    },
    created() {
        this.handlerForRouteChange();
    },
    mounted() {
        this.$nextTick(this.activatePlacesSearch);
    },
    props: [
        'pageType',
        'id'
    ],
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Locations' : 'Add Locations';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/locations/' + id)
                    .then((response) => {
                        this.form = response.data;
                        backend.getRequest('services/addresses/' + response.data.address + '/')
                            .then((response) => {
                                this.address = response.data;
                                this.isFetching = false;
                                this.errorFetching = false;
                            })
                            .catch(this.fetchErrorHandler);
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
 
            backend.getRequest('vbill/clients')
                .then((response) => {
                    this.clients = response.data.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.fetchErrorHandler);
        },
        clearForm() {
            this.form = {
                number: '',
                name: '',
                client: '',
                address: ''
            };
            this.address = {};
            setTimeout(() => this.$v.$reset(), 0);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = pickBy(this.form, (item) => item);
            if (this._props.pageType === 'add') {
                backend.postRequest('services/addresses/', this.address)
                    .then((response) => {
                        data.address = response.data.id;
                        backend.postRequest('services/locations/', data)
                            .then((response) => {
                                let addressName = response.data.name;
                                this.errorMessage = null;
                                this.successMessage = `${addressName} was added successfully`;
                                this.clearForm();
                            })
                            .catch(this.onSaveError);
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/locations/' + this.$route.params.id + '/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `${name} was changed successfully`;
                    })
                    .catch(this.onSaveError);
                backend.putRequest('services/addresses/' + this.address.id + '/', this.address)
                    .then(() => {

                    })
                    .catch(this.onSaveError);
            }
        },
        activatePlacesSearch() {
            // let defaultBounds = new google.maps.LatLngBounds(
            //     new google.maps.LatLng(-33.8902, 151.1759),
            //     new google.maps.LatLng(-33.8474, 151.2631));

            let input = this.$refs.inputAddress.$el;
            if (!input) {
                // return;
            }
            // let options = {
            //     bounds: defaultBounds,
            //     types: ['address']
            // };

            // let autocomplete = new google.maps.places.Autocomplete(input, options);
            // autocomplete.addListener('place_changed', () => {
            //     let place = autocomplete.getPlace();
            //     this.form.name = place.formatted_address;
            // });
        },
        onSaveError(error) {
            this.isSaving = false;
            this.successMessage = null;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        onFetchingError(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        fetchErrorHandler(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                'Something went wrong. Please try again later';
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
